<template>
    <div class="order-list">
        <div class="pro-box">
            <div class="info-box">
                <div class="info-title">{{infoTitleLeft}}</div>
                <div class="info-action" v-if="isRight">
                    <div v-if="!isReceive" @click="confirmReceive">确认收货</div>
                    <div class="info-action-action" v-else>
                        <div class="action-ok">已完成</div>
                        <div class="action-delete">
                            <van-icon size="19" name="delete" @click="remove" />
                        </div>
                    </div>
                </div>
            </div>

            <div>
                <div class="cell-list">
                    <!-- 商品信息 -->
                    <slot></slot>
                </div>

                <div class="bottom-info">
                    <div class="circle">
                        <div class="circle-box circle-left"></div>
                        <div class="circle-box circle-right"></div>
                    </div>
                    <div class="date" v-if="date != ''">{{date}}</div>
                    <div class="sum">
                        <div class="count">共计 {{count}} 件</div>
                        <div class="total">合计 ￥{{total}}</div>
                    </div>
                
                </div>

            </div>

        </div>
    </div>
</template>

<script>
import {toRefs,defineComponent} from 'vue';

export default defineComponent({
    name:'order-list',
    props:{
        infoTitleLeft: {
            type: String,
            default: '左边标题'
        },
        isRight: {
            type: Boolean,
            default: false
        },
        date: {
            type: String,
            default: '1970-01-01 00:00:00'
        },
        count: {
            type: Number,
            default: 0
        },
        total: {
            type: [Number, String],
            default: 0
        },
        isReceive: {
            type: Boolean,
            default: false
        }
    },
    setup (props,{emit}) {
        
        let confirmReceive = () => {
            emit('confirm');
        }

        let remove = () => {
            emit('remove');
        }
        return {
            ...toRefs(props),
            confirmReceive,
            remove
        }
    }
})
</script>

<style lang="less" scoped>
    .order-list{
        .pro-box{
            border-top-left-radius: 8px;
            border-top-right-radius: 8px;
            background-color: #fff;
            padding: 10px;
            .info-box{
                margin-bottom: 10px;
                display: flex;
                .info-title{
                    margin-right: auto;
                    font-size: 14px;
                }
                .info-action{
                    margin-left: auto;
                    font-size: 14px;
                    .info-action-action{
                        display: flex;
                        .action-ok{
                            
                        }
                        .action-delete{
                            margin-left: 10px;
                            width: 19px;
                            height: 19px;
                            
                        }
                    }
                }
            }
            .cell-list{
                padding-bottom: 20px;
                border-bottom: 1px dashed #e8e8e8;
            }
            .bottom-info{
                padding-top: 20px;
                font-size: 14px;
                position: relative;
                .circle{
                    position: absolute;
                    left: 0;
                    top: -10px;
                    width: 100%;
                    height: 20px;
                    display: flex;
                    .circle-box {
                        width: 20px;
                        height: 20px;
                        background-color: #f7f7f7;
                        border-radius: 50%;
                    }
                    .circle-left {
                        margin-right: auto;
                        position: relative;
                        left: -20px;
                    }
                    .circle-right {
                        margin-left: auto;
                        position: relative;
                        right: -20px;
                    }
                }
                .date{
                    margin-bottom: 10px;
                    font-size: 12px;
                }
                .sum{
                    display: flex;
                    .count{
                        margin-right: auto;
                    }
                    .total{
                        margin-left: auto;
                        color: #0c34ba;
                        font-weight: bold;
                    }
                }
            }
        }
    }
</style>